const baseUrl = 'https://pixabay.com/api/?image_type=photo&orientation=horizontal';

export default {
  page: 1,
  key: '15538572-d05d000357c1b063d561e0ca7',
  per_page: 12,
  query: '',
  fetchHits() {

    const requestParams = `&q=${this.query}&page=${this.page}&per_page=${this.per_page}&key=${this.key}`;

    return fetch(baseUrl + requestParams)
    .then(response => response.json())
    .then(parsedResponse => {
      this.incrementPage();
      return parsedResponse.hits;
    });
    },
    get searchQuery() {
      return this.query;
    },
    set searchQuery(value) {
      this.query = value;
    },
    incrementPage() {
      this.page +=1;
    },
    resetPage() {
      this.page = 1;
    }
  };





